<template>
  <div>
    <div class="totalTableSize">
      <div class="tipIcon"></div>
      <div class="totalCon">
        共有2632243条信息
      </div>
    </div>
    <div class="tableCon">
      <commonTable :columns="columns" :data="tableData" :pager="page"
                   @handleCurrentChange="handleCurrentChange">
      </commonTable>
    </div>
  </div>
</template>

<script>
  import USER from 'admin/service/user-service.js'
  export default {
    name: "Account",
    data() {
      return {
        columns: [
          {prop: 'name', label: '订单编号', width: '200', align: 'left'},
          {prop: 'detail', label: '商品名称', width: '300', align: 'left'},
          {prop: 'coin', label: '金额', align: 'right',width: '150'},
          {prop: 'coin', label: '兑换码', align: 'right',width: '150'},
          {prop: 'coin', label: '购买时间', align: 'right',width: '150'}
        ],
        tableData: [],
        page: {
          page: 1,
          unit: 10,
          total: 0
        }
      }
    },
    created(){
      this.getList();
    },
    methods: {
      //加载列表数据
      getList(){
        let data ={
          'page' : this.page.page,
          'unit': 10
        };
        USER.getUserList(data)
          .then(res => {
            console.log('res',res);
            if(res.code === "0"){
              this.tableData = res.data;
              this.page.total = res.count;
            }
          })
      },
      // 翻页处理
      handleCurrentChange(val) {

      },
      //打开新建页面
      goAddGoods(){
        this.$router.push({
          name:'AddGoods',
        })
      },
      //弹窗中确认按钮
      pSubmit(row) {

      }
    }
  }
</script>

<style lang="css" scoped>

</style>



